"use client";

import { useEffect, useState, useRef } from "react";
import { END_POINT } from "@/config";
import '@/styles/checkout.css';

const EGYPTIAN_GOVERNORATES = [
  'Cairo',
  'Giza',
  'Alexandria',
  'Dakahlia',
  'Sharqia',
  'Qalyubia',
  'Kafr El Sheikh',
  'Gharbia',
  'Monufia',
  'Beheira',
  'Ismailia',
  'Port Said',
  'Suez',
  'North Sinai',
  'South Sinai',
  'Damietta',
  'New Valley',
  'Matruh',
  'Qena',
  'Sohag',
  'Aswan',
  'Luxor',
  'Red Sea',
  'Beni Suef',
  'Faiyum',
  'Minya',
  'Asyut'
];

export default function Checkout() {
  const [formData, setFormData] = useState({
    fullName: '',
    email: '',
    phone: '',
    city: '',
    government: '',
    detailedAddress: '',
  });

  const [shippingCost, setShippingCost] = useState(0);
  const [shippingMessage, setShippingMessage] = useState('');

  const [isLoading, setIsLoading] = useState(true);
  const [govSearchTerm, setGovSearchTerm] = useState('');
  const [showGovDropdown, setShowGovDropdown] = useState(false);
  const govDropdownRef = useRef(null);

  useEffect(() => {
    // Fetch current user data from backend
    fetch(END_POINT.GET_CURRENT_USER, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      credentials: 'include',
    })
      .then(res => res.json())
      .then(json => {
        if (json.successful && json.data) {
          const user = json.data;
          const government = user.government || '';
          setFormData(prevData => ({
            ...prevData,
            fullName: user.name || '',
            email: user.email || '',
            phone: user.phone || '',
            government: government,
            detailedAddress: user.address || '',
          }));
          setGovSearchTerm(government);
          calculateShipping(government);
        }
      })
      .catch(error => {
        console.log('Error fetching user data:', error.message);
      })
      .finally(() => {
        setIsLoading(false);
      });
  }, []);

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (govDropdownRef.current && !govDropdownRef.current.contains(event.target)) {
        setShowGovDropdown(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  const calculateShipping = (gov) => {
    if (!gov) {
      setShippingCost(0);
      setShippingMessage('');
      return;
    }

    const normalizedGov = gov.toLowerCase().trim();

    if (normalizedGov === 'cairo' || normalizedGov === 'giza') {
      setShippingCost(50);
      setShippingMessage('');
    } else if (normalizedGov === 'qalyubia' || normalizedGov === 'qalubia') {
      setShippingCost(75);
      setShippingMessage('');
    } else {
      setShippingCost(0);
      setShippingMessage('Shipping not available directly. Arrange via external courier.');
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prevData => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleGovSearch = (e) => {
    const value = e.target.value;
    setGovSearchTerm(value);
    setShowGovDropdown(true);
    // Update formData if user is typing a valid governorate
    if (value && EGYPTIAN_GOVERNORATES.includes(value)) {
      setFormData(prevData => ({
        ...prevData,
        government: value,
      }));
      calculateShipping(value);
    } else if (!value) {
      // Clear government if search is cleared
      setFormData(prevData => ({
        ...prevData,
        government: '',
      }));
      calculateShipping('');
    }
  };

  const handleGovSelect = (governorate) => {
    setFormData(prevData => ({
      ...prevData,
      government: governorate,
    }));
    setGovSearchTerm(governorate);
    setShowGovDropdown(false);
    calculateShipping(governorate);
  };

  const filteredGovernorates = EGYPTIAN_GOVERNORATES.filter(gov =>
    gov.toLowerCase().includes(govSearchTerm.toLowerCase())
  );

  const subtotal = 120; // Hardcoded for now based on existing code
  const total = shippingMessage ? subtotal : subtotal + shippingCost;

  return (
    <div className="checkout-page">
      <h1 className="title">Checkout</h1>

      {/* ========= Customer Info ========= */}
      <section className="section">
        <h2 className="section-title">Customer Information</h2>

        <div className="inputs-grid">
          <input
            type="text"
            name="fullName"
            placeholder="Full Name"
            value={formData.fullName}
            onChange={handleInputChange}
          />
          <input
            type="email"
            name="email"
            placeholder="Email Address"
            value={formData.email}
            onChange={handleInputChange}
          />
          <input
            type="tel"
            name="phone"
            placeholder="Phone Number"
            value={formData.phone}
            onChange={handleInputChange}
          />
        </div>
      </section>

      {/* ========= Shipping Address ========= */}
      <section className="section">
        <h2 className="section-title">Shipping Address</h2>

        <div className="inputs-grid">
          <div className="gov-select-wrapper" ref={govDropdownRef}>
            <input
              type="text"
              name="government"
              placeholder="Search for your Governorate"
              value={govSearchTerm}
              onChange={handleGovSearch}
              onFocus={() => setShowGovDropdown(true)}
              className="gov-search-input"
            />
            {showGovDropdown && filteredGovernorates.length > 0 && (
              <div className="gov-dropdown">
                {filteredGovernorates.map((governorate) => (
                  <div
                    key={governorate}
                    className="gov-option"
                    onClick={() => handleGovSelect(governorate)}
                  >
                    {governorate}
                  </div>
                ))}
              </div>
            )}
          </div>
          <input
            type="text"
            name="city"
            placeholder="City"
            value={formData.city}
            onChange={handleInputChange}
          />
        </div>

        <textarea
          name="detailedAddress"
          placeholder="Detailed Address (Street, Building, Apartment, etc.)"
          value={formData.detailedAddress}
          onChange={handleInputChange}
          className="detailed-address-textarea"
        />
      </section>

      {/* ========= Payment Method ========= */}
      <section className="section">
        <h2 className="section-title">Payment Method</h2>

        <div className="payment-box">
          <label>
            <input type="radio" name="payment" defaultChecked />
            Cash on Delivery
          </label>

          <label style={{ opacity: 0.5, cursor: 'default' }}>
            <input type="radio" name="payment" disabled />
            Credit Card (Soon)
          </label>
        </div>
      </section>

      {/* ========= Order Summary ========= */}
      <section className="section summary">
        <h2 className="section-title">Order Summary</h2>

        <div className="summary-item">
          <span>Subtotal</span>
          <span>{subtotal.toFixed(2)} EGP</span>
        </div>

        <div className="summary-item">
          <span>Shipping</span>
          <span>{shippingMessage ? 'To be determined' : `${shippingCost.toFixed(2)} EGP`}</span>
        </div>
        {shippingMessage && (
          <div className="summary-note">
            <small style={{ color: '#e67e22' }}>{shippingMessage}</small>
          </div>
        )}

        <div className="summary-total">
          <span>Total</span>
          <span>{total.toFixed(2)} EGP</span>
        </div>
      </section>

      {/* ========= Place Order Button ========= */}
      <button className="place-order-btn">Place Order</button>
    </div>
  );
}
